import React, { Component } from 'react';
import axios from 'axios';

class FormMovie extends Component {
    constructor(props) {
      super(props);
  
      this.state = {
      }
  
      /* this.handleInput = this.handleInput.bind(this); */
      this.handleSubmit = this.handleSubmit.bind(this);
    }
  
    /* handleInput(e) {
			const { name, value } = e.target
			
      MoviesCopy[name] = value;
  
      this.setState({movies: MoviesCopy})
      
    } */
  
    async handleSubmit(e) {
			e.preventDefault();
			console.log(e.target[0].value)
			const page = 1;
			const responde = await axios.get(`http://www.omdbapi.com/?apikey=c9d02680&s=transformers&${page}`);
			const movies = responde.data.Search;	
      this.props.onAddMovies(movies, page);
    }
  
      render() {
          return (
              <form onSubmit={this.handleSubmit}>
          <div className="form-group">
            <h2 >Search Movies...</h2>
            <input className="form-control" name="title" placeholder="Enter Title Movie" onChange={this.handleInput} />
          </div>
          <button type="submit" className="btn btn-primary">Submit</button>
        </form>
          );
      }
  }
  export default FormMovie;

